.BlackJack__area {
    position: relative;
    min-height: 750px;
    background: url('img/table-texture2.jpg') no-repeat;
    background-size: cover;
    background-position: center;
}

.Card__table .overlay {
    position: absolute;
    width: 100%;
    height: 100%;
    background: white;
    z-index: 10;
}

.Card__container {
    position: relative;
    padding: 80px 0;
}

.Card__container.three-box {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: center;
        justify-content: center;
}

#Selected {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: center;
        justify-content: center;
    -ms-flex-align: center;
        align-items: center;
    position: absolute;
    width: 100%;
    height: 100%;
    min-height: 300px;
    background: rgba(0, 0, 0, 0.95);
    color: #fff;
    font-weight: bold;
    font-size: 32px;
    z-index: 1;
}

/* turn off displaying game */
.overlay {
    position: absolute;
    width: 100%;
    height: 100%;
    background: white;
    /* opacity: 0.7; */
    z-index: 10;
}

/* game options */
.options {
    position: absolute;
    left: 15px;
    top: 50%;
    -webkit-transform: translateY(-50%);
        -ms-transform: translateY(-50%);
            transform: translateY(-50%);
}

.options button {
    min-width: 35px;
    margin-right: 10px;
    padding: 5px;
    background: white;
    border: 0;
    color: #ff0000;
    border-bottom: 2px solid lightgrey;
}

.options button.speech-btn {
    float: right;
    border-bottom: 0;
}

.speech-btn span {
    vertical-align: middle;
}

.speech-btn span:first-child {
    margin-right: 5px;
}

.options button:active {
    border-color: #990000;
}

.options button:focus {
    outline: 0;
}

.options.options-display {
    position: fixed;
    top: auto;
    -webkit-transform: translateY(-100%);
        -ms-transform: translateY(-100%);
            transform: translateY(-100%);
    right: 0 !important;
    left: auto;
    z-index: 100;
}

.options-display button {
    margin-bottom: 10px;
    margin-right: 15px;
    border: none;
    background: none;
    -webkit-text-stroke-width: 1px;
    -webkit-text-stroke-color: white;
    font-weight: bold;
}

.options-display button:active {
    color: lightgrey;
}

/* game score */
.score {
    position: fixed;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: center;
        justify-content: center;
    -ms-flex-align: center;
        align-items: center;
    top: 100px;
    left: 15px;
    padding: 10px;
    border-radius: 5px;
    font-size: 36px;
    font-weight: 800;
    color: white;
    -webkit-text-stroke: 1px black;
}

.score span {
    display: -ms-flexbox;
    display: flex;
}

.score span img {
    height: 50px;
    margin-left: 10px;
}

/* responsive design */
@media only screen and (max-width: 480px) {
    .speech-btn span:nth-child(2) {
        display: block;
        font-size: 11px;
    }
}


